.wrapper {
  width: 1800px;
  height: 500px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  /* 隐藏超出容器的内容 */
  flex-shrink: 0;
  /* 禁止项目缩小 */
  flex-wrap: nowrap;
  animation: fadeIn 1s ease-in-out;
}
.wrapper ._box {
  width: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.wrapper ._box img {
  box-shadow: 8px 8px 10px rgba(0, 0, 0, 0.2);
}
.wrapper ._box .overlay {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #b8c1ec;
  opacity: 0.9;
  text-align: center;
  overflow: hidden;
  transition: transform 0.5s;
  transform-origin: top;
  /* 设置变换的原点为顶部 */
  transform: scaleY(1);
  /* 初始缩放为1 */
}
.wrapper ._box .overlay:hover {
  cursor: pointer;
  transform: scaleY(0);
  /* 鼠标悬停时垂直方向缩放为0，实现向上缩小并消失的效果 */
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* 或者使用百分比 */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
